import { createBrowserRouter } from "react-router-dom";
import App from "./App";
import Coins from "./componts/Coins";
import Coin from "./componts/Coin";
import Price from "./componts/Price";
import CandleStick from "./componts/CandleStick";
import LineChart from "./componts/LineChart";
import { basename } from "path";

const router = createBrowserRouter(
  [
    {
      path: "/",
      element: <App />,
      children: [
        {
          path: "",
          element: <Coins />,
        },
        {
          path: ":coinId",
          element: <Coin />,
          children: [
            {
              path: "price",
              element: <Price />,
            },
            {
              path: "CandleStick",
              element: <CandleStick />,
            },
            {
              path: "lineChart",
              element: <LineChart />,
            },
          ],
        },
      ],
    },
  ],
  { basename: `${process.env.PUBLIC_URL}/` }
);
export default router;
